@import '~sass-mq';

$color-primary: #3dd800;
$color-darker-primary: #24610b;
$color-white: #fff;
$color-black: #000000;
$color-grey: #686767;
$color-grey-lighter: #F5F5F5;

$timing: 0.35s;
$transition-timing-function: ease;
$transition: $timing $transition-timing-function;

$mq-breakpoints: (
  tablet-landscape: 1024px,
);

$box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, .08);
$form-field-text-color: #757575;

:root {
  --color-primary: #{$color-primary};
  --color-primary-darker: #{$color-darker-primary};
  --color-white: #{$color-white};
  --color-black: #{$color-black};
  --color-grey: #{$color-grey};
  --color-grey-lighter: #{$color-grey-lighter};
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}


body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: 'Montserrat', sans-serif;
  font-size: 1.6rem;
  color: var(--color-black);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  color: inherit;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

button {
  padding: 0;
  cursor: pointer;
  background: 0;
  border: 0;

  * {
    pointer-events: none;
  }
}

button,
a,
input {
  outline: none;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

input {
  padding: 0;
  border: 0;
}

main{
  padding: 0 10px;
}